// frontend/pages/dashboard/index.js
import { useEffect, useState } from 'react';
import api from '../../utils/api';
import { useRouter } from 'next/router';

export default function Dashboard() {
  const [stats, setStats] = useState({ posts: 0, pending: 0, users: 0, views: 0 });
  const router = useRouter();

  useEffect(() => {
    const token = localStorage.getItem('token');
    if (!token) router.push('/login');
    else loadStats();
  }, []);

  const loadStats = async () => {
    try {
      const res = await api.get('/admin/stats');
      setStats(res.data);
    } catch (err) {
      console.error('Error loading stats', err);
    }
  };

  return (
    <div className="p-6 bg-gray-50 min-h-screen">
      <h1 className="text-2xl font-bold mb-6">অ্যাডমিন ড্যাশবোর্ড</h1>
      <div className="grid grid-cols-2 md:grid-cols-4 gap-6">
        <div className="bg-white p-4 shadow rounded">
          <h2 className="text-gray-600">মোট পোস্ট</h2>
          <p className="text-3xl font-bold text-blue-600">{stats.posts}</p>
        </div>
        <div className="bg-white p-4 shadow rounded">
          <h2 className="text-gray-600">Pending পোস্ট</h2>
          <p className="text-3xl font-bold text-orange-600">{stats.pending}</p>
        </div>
        <div className="bg-white p-4 shadow rounded">
          <h2 className="text-gray-600">মোট ইউজার</h2>
          <p className="text-3xl font-bold text-green-600">{stats.users}</p>
        </div>
        <div className="bg-white p-4 shadow rounded">
          <h2 className="text-gray-600">মোট ভিউ</h2>
          <p className="text-3xl font-bold text-purple-600">{stats.views}</p>
        </div>
      </div>
    </div>
  );
}

